.Banner {
    max-height: 75vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url("../banner.jpg");
    opacity: 1;
    display: flex;
    align-items: center;
    overflow: hidden;
    padding-top: 10rem;
    overflow: hidden;
    z-index: -10;

}
.container {
    margin: auto;
    opacity: 1;
}
.Bannercon {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.people {
    position: relative;
    top: 0;
    left: 100px;
    right: 0;
    z-index: 1;
    width: 500px;
    opacity: 0;
}
.Bannercon .bleft{
    width: 500px;
    text-align: left;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    flex-wrap: wrap;
    position: relative;
    right: -200px;
}

.Bannercon .bleft h4 {
    color: #F9934E;
    font-size: 2rem;
    margin: 0px;
    margin-bottom: 0px;
}

.Bannercon .bleft h1 {
    font-size: 4rem;
    margin-top: 0px;
}

.Bannercon .bleft p {
    font-size: 1.2rem;
    margin: 0px;
}

.Bannercon .bleft h5{
    font-size: 1.2rem;
    margin-top: 0px;
    margin-bottom: 2rem;
}

.buttonviewwork {
    padding: 10px 50px;
    border-radius: 25px;
    color: white;
    background-color: #F9934E;
    box-shadow: 0px 0px 20px rgba(249, 147, 78, 0.6);
    cursor: pointer;
    transition: 0.5s ease-in-out;
    border: 1px solid #F9934E;
    outline: none;
    
}

.buttonviewwork:hover {
    background-color: #fff;
    color:#F9934E;
    border: 1px solid #F9934E;
    transition: 0.3s;
}

.contact {
    display: flex;
    margin-top: 200px;
}
.contact ul {
    display: flex;
    list-style: none;
    padding: 20px;
}

.contact ul li {
    width: 40px;
    height: 40px;
    padding: 0px;
    background-color: none;
    box-shadow: 0px 0px 1px rgba(0,0,0,0.3);
    margin-right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border:#ccc;
    background-color: #fff;
    border-radius: 25px;
    opacity: .9;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.contact ul li:hover {
    background-color: #F9934E;
    color: white;
    opacity: 1;
    transition: 0.3s ease-in-out;
}

.contact ul li:first {
    margin-right: 0px;
}

.contact ul li a{
    font-size: 1.5rem;
    margin-bottom: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}


@media screen and (max-width: 480px) {

    .Banner {
        background-size: cover;
    }
    .Bannercon {
        padding-left: 80px;
    }

    .Bannercon .bleft{
        width: 200px;
        left: 50px;
    }
    .Bannercon .bleft h4 {
        margin-top: 20px;
        font-size: 1.2rem;
        
    }

    .Bannercon .bleft h1 {
        font-size: 1.8rem;
    }

    .Bannercon .bleft p {
        font-size: 1rem;
    }
    .Bannercon .bright {
        display: none;
    }

    .contact {
        margin-top: 0px;
    }

    .contact ul {
        margin-left: 80px;
    }

    .overlayline {
        width: 200px;
    }
}

@media screen and (max-width: 768px) {
    .Bannercon {
        padding-left: 10px;
    }
    .Bannercon .bleft h4 {
        margin-top: 20px;
        font-size: 1.2rem;
        
    }

    .Bannercon .bleft h1 {
        font-size: 1.8rem;
        margin: 0px;
        
    }
    .Bannercon .bleft p {
        font-size: 1rem;
    }
    .Bannercon .bright {
        display: none;
    }

    .contact {
        margin-top: 80px;
        opacity: 0;
    }

    .contact ul {
        margin-left: 80px;
    }

    .overlayline {
        width: 200px;
    }
}