
section {
    min-height: 100vh;
    background-color: #fff;
}
    

#aboutsec {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.aboutcon {
    display: flex;
    width: 70%;
    margin: 0px auto;
}

#aboutsec h1 {
    font-size: 3rem;
}
/* .aboutcon .cards {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-direction: column;
} */

.aboutcon .conleft {
    width: 50%;
}
.aboutcon .conright {
    width: 50%;
}
.aboutcon .conleft .cards ul {
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    margin: 0px auto;
}

.aboutcon .conleft .cards ul li {
    width: 300px;
    display: flex;


}

.aboutcon .conleft .cards ul li .icon {
    font-size: 1.5rem;
    padding-right: 15px;
    color: #F9934E;
}

.aboutcon .conleft .cards ul li .text {
    text-align: left;
}

.aboutcon .conleft .cards ul li .text h4 {
    margin: 0px;

}
.aboutcon .conright .items ul {
    list-style: none;
}

.aboutcon .conright .items ul li {
    text-align: left;
}


.Carditemlist {
    margin: 0px auto;
    width: 70%;
    display: flex;
    justify-content: center;   
}

.propitem ul {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

.propitem ul li {
    border-radius: 1px solid #ccc;
    width: 400px;
    height: 150px;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-right: 1.2rem;
    margin-top: 1rem;
    justify-content: center;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
    transition: 0.4s ease;
    border-radius: 10px;
    cursor: pointer;
}

.propitem ul li .icon {
    color: #F9934E;
    font-size: 3rem;
}

.propitem ul li:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
    transition: 0.4s ease-out;
    
}



#educatesec .Container {
    margin: 0px auto;
    width: 70%;
    max-width: 960px;
}

#educatesec .Container h1 {
    font-size: 3rem;
    margin: 0px;
}

.CardEdu {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin: 0px;
    padding: 0px;
    max-width: 960px;
    margin: 0px auto;
}
.CardEdu li {
    display: flex;
    justify-content: center;
    height: auto;
    margin-top: 5px;
}
.CardEdu li .cicle {
    background-color: #F9934E;
    width: 50px;
    height: 50px;
    display: flex;
    position: absolute;
    border-radius: 50%;
    z-index: 2;
}

.CardEdu li .Arrow {
    width: 100px;
    height: 5px;
    background-color: #000;
    position: relative;
    z-index: 1;
    top: 20px;
    left: 200px;
}
.CardEdu li .cardtext {
    z-index: 4;
    background-color: #fff;
    position: relative;
    padding: 20px;
    width: 300px;
    height: 300px;
    left: 12rem;
    border-radius: 15px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
}


.CardEdu li:nth-child(2) .Arrow {
    width: 100px;
    height: 5px;
    background-color: #000;
    position: relative;
    z-index: 1;
    top: 20px;
    left: 120px;
}

.CardEdu li:nth-child(2) .cardtext {
    z-index: 10;
    background-color: #fff;
    position: relative;
    padding: 20px;
    width: 300px;
    height: 300px;
    z-index: 4;
    left: -18rem;
    opacity: 1;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
}

.img-thumnail img {
    width: 150px;
    height: 150px;
}


#introsec .container {
    margin: 0px auto;
    width: 70%;
}
#introsec .container h1 {
    font-size: 3rem;
}

#introsec .container p {
    text-align: left;
    font-size: 1.5rem;
}

#introsec .container h3 {
    text-align: right;
    font-size: 1.3rem;
}



#worksec {
    display: flex;
    justify-content: center;
    background-color: #F9934E;
    margin-top: 5rem;
}

#worksec .container {
    width: 70%;
}

#worksec .container h1 {
    font-size: 3rem;
    color: white;

}

.mapwork {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1000px;
    margin: 0px auto;
    flex-wrap: wrap;
}

.mapwork .items {
    width: 300px;
    height: auto;
    padding: 5px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6);
    margin-right: 1rem;
    margin-top: 2rem;
}

#footer {
    background-color: #000;
}

#worksec {
    height: auto;
    padding: 1.5rem;
}

#worksec .container h2 {
    color: white;
}


#certificate {
    height: auto;
    padding: 10px;
}

#certificate .container h1 {
    font-size: 3rem;
}


@media screen and (max-width: 480px) {
    
    .aboutcon {
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .aboutcon .conleft {
        width: 100%;
    }
    .aboutcon .conright {
        width: 100%;
    }

    .propitem ul {
        display: flex;
        flex-direction: column;
    }
    

    .propitem ul li {
        width: 300px;
    }

    #introsec .container p {
        font-size: 1rem;
    }

    #introsec .container {
        width: 90%;
    }
    .CardEdu {
        display: flex;
    }
    .CardEdu li {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-bottom: 0rem;
        margin-top: 0px;
        height: 400px;
    }

    .CardEdu li .Arrow {
        width: 5px;
        height: 100px;
        background-color: #000;
        position: relative;
        z-index: 1;
        top: -200px;
        left: 0px;
    }
    .CardEdu li .cicle {
        width: 80px;
        height: 80px;
    }
    .CardEdu li .cardtext {
        z-index: 10;
        background-color: #FFF;
        position: relative;
        padding: 20px;
        width: 250px;
        height: 250px;
        left: 0px;
        top: 250px;
    }

    .CardEdu li .cardtext h3 {
        margin: 0px;
    }

    .CardEdu li .cardtext p {
        margin: 0px;
    }

    #educatesec .Container {
        width: 90%;
        padding: 0px 10px;
    }


    .CardEdu li:nth-child(2) .Arrow {
        width: 5px;
        height: 100px;
        background-color: #000;
        position: relative;
        z-index: 1;
        top: 200px;
        right: 0px;
        left: 0px;
    }
    
    .CardEdu li:nth-child(2) .cardtext {
        z-index: 2;
        background-color: #FFF;
        position: relative;
        padding: 20px;
        width: 250px;
        height: 250px;
        left: 0rem;
    }
    

    .CardEdu li {
        display: flex;
        justify-content: center;
        height: auto;
        margin-bottom: 10rem;
    }

    #worksec .container {
        padding: 5px 10px;
    }

    


}


