section {
    min-height: 100vh;
}

.logo {
    width: 50px;
    height: 50px;
}

.header {
    height: 120px;
    align-items: center;
    transition: all 0.3s ease-in-out;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: center;
    z-index: 99;
}



.header.active {
    height: 100px;
    background-color: #fff;
    box-shadow: 0px 0px 20px rgba(0,0,0,0.5);
}

.navcon {
    display: flex;
    width: 100%;
    max-width: 960px;
    justify-content: space-between;
    height: 120px;
    align-items: center;
    padding: 0px 10px;
}

.nav-menu ul {
    display: flex;
    list-style: none;
}

.nav-menu ul li {
    padding-right: 30px;
    display: block;
    position: relative;
    z-index: 1;
    padding: 14px;
    display: flex;
    justify-content: center;
}

.nav-menu ul li a {
    color: #000;
    text-decoration: none;
}
.nav-menu ul li.active .overlay .arrow {
    display: flex;
    transition: 0.4s ease;
    opacity: 1;
    transform: translateX(0px);
}


.nav-menu ul li:hover .overlay .arrow {
    display: flex;
    transition: 0.4s ease;
    opacity: 1;
    transform: translateX(0px);
}

.nav-menu ul li:hover a {
    color: #F9934E;
}

.arrow {
    opacity: 0;
    width: 3px;
    height: 50px;
    background-color: #F9934E;
    position: relative;
    top: -50px;
    left: 10px;
    transition: 0.4s ease;
    transform: translateY(-80px);
}

.overlay {
    position: absolute;
    width: 20px;
    height: 50px;
    z-index: -9;
}

.navbar-toggle {
    display: none;
}

.bar {
    font-size: 2rem;
    position: relative;
    right: 20px;
}


.navbar-toggle {
    transition: 0.3s;
}


@media screen and (max-width: 480px) {

    .navbar-toggle {
        display: flex;
    }

    .logocon {
        padding-left: 40px;
    }

    .nav-menu ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 100px;
        left: 0;
        right: 0;
        transform: translateY(-500px);
        background-color: #fff;
        opacity: 0;
        z-index: -5;

    }
    .nav-menu.active ul {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 105px;
        left: 0;
        right: 0;
        opacity: 1;
        transform: translateY(0px);
        transition: .3s ease;
        background-color: #fff;
        border-radius: 1px;
        z-index: 1;

    }

    .nav-menu ul li {
        padding-right: 0px;
        display: flex;
        justify-content: left;
    }

    .overlay .arrow {
        opacity: 0;
        width: 3px;
        height: 20px;
        background-color: #F9934E;
        position: relative;
        top: 0px;
        left: -10px;
        transition: 0.4s ease;
        transform: translateY(-80px);
    }

    

}

